// RESET LDP
.title-cate {
    font-size: 24px;
    line-height: 1.4;
    font-weight: 500;
    margin-bottom: 15px;
    br {
        display: none;
    }
    span {
        color: #FF5E57;
    }
    &.txt-20 {
        font-size: 20px;
    }
    &.txt-32 {
        font-size: 32px;
    }
    &.bolb {
        font-weight: bold;
    }
    @media (max-width: 1025px) {
        font-size: 20px;
    }
    @media (max-width: 600px) {
        text-align: center;
        font-size: 16px!important;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 10px;
        color: var(--colormain);
        br {
            display: block;
        }
        span {
            color: #FF5E57;
        }
        &.txt-32 {
            font-size: 24px !important;
            text-transform: none;
            font-weight: 500;
        }
    }
}

.head-course {
    margin-bottom: 30px;
    display: flex;
    align-items: end;
    gap: 60px;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        background-color: #f1f8f5;
        height: 3px;
        left: 0;
        right: 380px;
        bottom: 0;
    }
    &__left {
        width: 100%;
    }
    &__right {
        width: 320px;
        flex-shrink: 0;
    }
    .tab-default {
        border-bottom: 0;
    }
    @media (max-width: 1480px) {
        gap: 20px;
        &::before {
            right: 0;
            bottom: -6px;
        }
        .tab-default a.active::before {
            bottom: -6px;
        }
    }
    @media (max-width: 1025px) {
        flex-wrap: wrap;
        gap: 10px;
        &__right {
            width: 100%;
        }
        .tab-default {
            border-bottom: 3px solid #f1f8f5;
            a {
                &.active {
                    &::before {
                        bottom: -3px;
                    }
                }
            }
        }
        &::before {
            display: none;
        }
    }
    @media (max-width: 600px) {
        margin-bottom: 15px;
        .line-tab {
            position: relative;
            &::before {
                content: "";
                background: #fff;
                position: absolute;
                width: 100%;
                height: 3px;
                bottom: 3px;
            }
        }
        .tab-default {
            border-bottom: 0;
            padding-bottom: 3px;
            a {
                &.active {
                    &::before {
                        bottom: 0;
                    }
                }
            }
        }
    }
}

.section-padding {
    padding: 0 30px;
    @media (max-width: 1480px) {
        padding: 0 20px;
    }
    @media (max-width: 600px) {
        padding: 0 15px;
    }
}

.btn-gradient {
    border-radius: 4px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 0 16px;
    &::before {
        content: "";
        background: linear-gradient(279deg, #F9A826 0%, #68BBA0 100%);
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        border-radius: 4px;
    }
    &::after {
        content: "";
        background: #fff;
        position: absolute;
        left: 1px;
        top: 1px;
        right: 1px;
        bottom: 1px;
        border-radius: 4px;
    }
    span {
        background: linear-gradient(279deg, #F9A826 0%, #68BBA0 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 16px;
        position: relative;
        z-index: 1;
        display: inline-flex;
        align-items: center;
        gap: 2px;
        .ic {
            background: url(images/graphics/bg-dotted.png) no-repeat;
            width: 18px;
            height: 18px;
            background-size: contain;
        }
    }
    &.video {
        &::before {
            background: linear-gradient(279deg, #68BBA0 0%, #007BBD 100%);
        }
        span {
            background: linear-gradient(279deg, #68BBA0 0%, #007BBD 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            .ic {
                background: url(images/graphics/ic-video.png) no-repeat;
                background-size: contain;
            }
        }
    }
    &.comment {
        &::before {
            background: linear-gradient(279deg, #A0D9BA 0%, #2E8B57 100%);
        }
        span {
            background: linear-gradient(279deg, #A0D9BA 0%, #2E8B57 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            .ic {
                background: url(images/graphics/ic-thaoluan.png) no-repeat;
                background-size: contain;
            }
        }
    }
    @media (max-width: 600px) {
        padding: 0 10px;
        span {
            font-size: 13px;
        }
    }
}

.padding-line-bottom {
    border-bottom: 1px solid #dbe2df;
    padding: 20px 30px;
    @media (max-width: 1480px) {
        padding: 20px;
    }
}

.filter-control {
    position: static;
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 4px;
    border-radius: 4px;
    padding: 4px;
    background: #FEFEFE;
    color: #656867;
    transition: 0.2s all;
    a {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 36px;
        border: 1px solid #DBE2DF;
        border-radius: 4px;
        padding: 0 12px;
        color: #656867;
        font-size: 16px;
        font-weight: 300;
        transition: 0.2s all;
        &.active,
        &:hover {
            background: #F1F8F5;
            border: 1px solid #F1F8F5;
            color: var(--colormain);
        }
        &.ic {
            border: 0;
        }
    }
    @media (max-width: 600px) {
        position: sticky;
        z-index: 8;
        box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.16);
        display: flex;
        flex-wrap: nowrap;
        white-space: nowrap;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
        -ms-overflow-style: -ms-autohiding-scrollbar;
        &::-webkit-scrollbar {
            display: none;
        }
        &.sticky--active {
            margin-left: -15px;
            margin-right: -15px;
            border-radius: 0;
            padding: 10px 16px;
        }
    }
}

// END RESET LDP
.main-grid {
    display: flex;
    flex-wrap: wrap;
}

.main-right {
    width: calc(100% - 340px);
    position: relative;
    .img-bg {
        position: absolute;
        right: 0px;
        top: 0px;
        overflow: hidden;
        z-index: -1;
        img {
            transform: translate(45px, -65px);
        }
    }
    @media (max-width: 1480px) {
        width: calc(100% - 310px);
    }
    @media (max-width: 1025px) {
        width: calc(100% - 80px);
    }
    @media (max-width: 600px) {
        width: 100%;
        .img-bg {
            img {
                transform: translate(55px, -8px);
                max-width: 210px;
            }
        }
    }
}

.section-banner {
    background: #25644d url(images/graphics/bg-banner.jpg) no-repeat top;
    background-size: cover;
    margin-top: -70px;
    padding: 30px 30px 60px 30px;
    color: #fff;
    .grid-home {
        align-items: center;
    }
    .banner-mobile {
        display: none;
    }
    .content {
