        font-size: 48px;
        font-weight: 500;
        width: 100%;
        line-height: 1.3;
    }
    .des {
        width: 100%;
        font-size: 18px;
    }
    .price {
        width: 100%;
        display: flex;
        column-gap: 20px;
        row-gap: 20px;
        align-items: center;
        .price-old {
            font-size: 20px;
            text-decoration-line: line-through;
            color: #F9A825;
        }
        .price-new {
            font-size: 32px;
            font-weight: 500;
            color: #F9A825;
        }
    }
    .img {
        text-align: right;
        margin-bottom: -60px;
    }
    @media (max-width: 1480px) {
        padding: 20px 20px 60px 20px;
        .title {
            font-size: 36px;
        }
    }
    @media (max-width: 1280px) {
        .title {
            font-size: 32px;
        }
    }
    @media (max-width: 1025px) {
        margin-bottom: 20px;
        padding: 20px;
        .content {
            column-gap: 10px;
            row-gap: 10px;
        }
        .title {
            font-size: 26px;
        }
        .des {
            font-size: 16px;
        }
        .price {
            .price-old {
                font-size: 16px;
            }
            .price-new {
                font-size: 24px;
            }
        }
        .img {
            margin-bottom: -20px;
        }
    }
    @media (max-width: 600px) {
        margin-top: 0;
        padding: 0;
        .grid-home {
            display: none;
            gap: 10px
        }
        .banner-mobile {
            display: block;
        }
        .title {
            font-size: 22px;
        }
        .des {
            font-size: 14px;
        }
        .price {
            column-gap: 10px;
            row-gap: 10px;
            .price-old {
                font-size: 14px;
            }
            .price-new {
                font-size: 20px;
            }
        }
        .img {
            width: 150px;
            flex-shrink: 0;
        }
    }
}

.section-sukien {
    margin-top: -120px;
    margin-bottom: 30px;
    position: relative;
    z-index: 1;
    .grid {
        column-gap: 30px;
        row-gap: 30px;
    }
    .title {
        font-size: 16px;
        color: #fff;
    }
    .article-itemt.full {
        text-align: center;
        gap: 8px;
        .thumb_img {
            border-radius: 12px;
            border: 2px solid #F1F8F5;
            img {
                -webkit-transition: -webkit-transform .3s ease-out, opacity .35s;
                -webkit-transition: opacity .35s, -webkit-transform .3s ease-out;
                transition: opacity .35s, -webkit-transform .3s ease-out;
                transition: transform .3s ease-out, opacity .35s;
                transition: transform .3s ease-out, opacity .35s, -webkit-transform .3s ease-out;
                backface-visibility: hidden;
            }
            &:hover {
                img {
                    -webkit-transform: scale(1.2);
                    -ms-transform: scale(1.2);
                    transform: scale(1.2);
                    -webkit-transition: -webkit-transform 4s ease-out;
                    transition: -webkit-transform 4s ease-out;
                    transition: transform 4s ease-out;
                    transition: transform 4s ease-out, -webkit-transform 4s ease-out;
                }
            }
        }
        .article-content {
            justify-content: center;
            gap: 4px;
        }
    }
    @media (max-width: 1480px) {
        .grid__5 {
            grid-template-columns: repeat(4, 1fr);
            .article-itemt {
                &:nth-child(5) {
                    display: none;
                }
            }
        }
    }
    @media (max-width: 1025px) {
        margin-top: 0px;
        .grid {
            column-gap: 10px;
            row-gap: 10px;
        }
        .title {
            color: #00110b;
        }
    }
    @media (max-width: 600px) {
        margin-bottom: 15px;
        .title {
            text-transform: uppercase;
            font-weight: 700;
            color: #034C31;
            text-align: center;
            margin-bottom: 15px;
            span {
                color: #FF5E57;
            }
        }
        .grid {
            margin-right: -15px;
            padding-right: 15px;
            width: calc(100% + 15px);
            column-gap: 15px;
            row-gap: 15px;
            scroll-behavior: smooth;
            -ms-overflow-style: -ms-autohiding-scrollbar;
            overflow-x: auto;
            -webkit-overflow-scrolling: touch;
            -webkit-scroll-snap-type: x mandatory;
            -ms-scroll-snap-type: x mandatory;
            scroll-snap-type: x mandatory;
            white-space: nowrap;
            font-size: 0;
            &::-webkit-scrollbar {
                display: none;
            }
            .article-itemt {
                width: 172px;
                scroll-snap-align: start;
            }
        }
    }
}

.main-content {
    padding: 30px;
    margin-right: -30px;
    margin-bottom: 50px;
    border-radius: 16px 0px 0px 16px;
    overflow: hidden;
    background: #FBFDFC;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.16);
    .mr-ml {
        margin-left: -30px;
        margin-right: -30px;
    }
    .on-margin {
        margin: -30px -30px 20px -30px;
    }
    @media (max-width: 1480px) {
        padding: 20px;
        margin-right: -20px;
        .mr-ml {
            margin-left: -20px;
            margin-right: -20px;
        }
        .on-margin {
            margin: -20px -20px 20px -20px;
        }
    }
    @media (max-width: 600px) {
        margin-bottom: 30px;
        margin-right: 0;
        padding: 0;
        border-radius: 0;
        border: 0;
        box-shadow: none;
        background: none;
        .mr-ml {
            margin-left: 0px;
            margin-right: 0px;
        }
        .on-margin {
            margin: 0px 0px 20px 0px;
        }
