}

.box-grid-time {
    column-gap: 10px;
    row-gap: 10px;
    border-bottom: 3px solid #F1F8F5;
    margin-bottom: 30px;
    padding-bottom: 30px;
    .box-item {
        border-radius: 4px;
        background: #F1F8F5;
        padding: 10px;
        .title {
            font-size: 18px;
            margin-bottom: 4px;
            font-weight: 400;
            margin-top: 3px;
        }
        .time {
            font-weight: 300;
        }
    }
    .box-time {
        display: flex;
        align-items: center;
        gap: 20px;
        justify-content: end;
    }
    @media (max-width: 1480px) {
        display: flex;
        .box-item {
            width: 100%;
            .title {
                font-size: 16px;
            }
        }
        .follow-us {
            width: 395px;
            flex-shrink: 0;
        }
    }
    @media (max-width: 1025px) {
        margin-bottom: 20px;
        padding-bottom: 20px;
        .follow-us {
            display: none;
        }
    }
    @media (max-width: 600px) {
        border-bottom: 0;
        padding-bottom: 0;
        margin-bottom: 15px;
        .box-time {
            border-radius: 8px;
            border: 1px solid #dbe2df;
            background: #fefefe url(images/icons/cup.png) no-repeat top -13px left -8px;
            gap: 12px;
            color: var(--colormain);
            padding: 3px;
            .text-right {
                text-align: center!important;
            }
            .title {
                font-size: 14px;
                font-weight: 700;
                padding: 0 15px;
                line-height: 1.6;
            }
            .time {
                display: none;
            }
            .countdown {
                border-radius: 8px;
                background: #f1f8f5;
                gap: 15px;
                padding: 4px 6px;
            }
        }
    }
}

.follow-us {
    display: flex;
    align-items: center;
    gap: 10px;
    .list-user {
        display: flex;
        align-items: center;
        .ava {
            position: relative;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            overflow: hidden;
            margin-left: -12px;
            &:nth-child(1) {
                margin-left: 0px;
                z-index: 5;
            }
            &:nth-child(2) {
                z-index: 4;
            }
            &:nth-child(3) {
                z-index: 3;
            }
            &:nth-child(4) {
                z-index: 2;
            }
            &:nth-child(5) {
                z-index: 1;
            }
        }
    }
    .des {
        font-size: 16px;
        a {
            color: var(--colormain);
        }
    }
    @media (max-width: 1480px) {
        .des {
            font-size: 14px;
        }
    }
}

.countdown {
    display: flex;
    gap: 20px;
    .item_countdown {
        text-align: center;
        color: #023D28;
        font-weight: 300;
        position: relative;
        &::after {
            content: ":";
            position: absolute;
            top: 13px;
            right: -10px;
        }
        &:last-child::after {
            display: none;
        }
        .item_txt {
            span {
                font-size: 18px;
                font-weight: 500;
            }
        }
    }
    @media (max-width: 600px) {
        .item_countdown {
            &::after {
                right: -8px;
                top: 0px;
                font-weight: 700;
            }
            .item_txt {
                font-size: 12px;
                line-height: 1.5;
                span {
                    font-size: 16px;
                }
            }
        }
    }
}

.list-course {
    column-gap: 30px;
    row-gap: 50px;
    .article-itemt {
        border-radius: 8px;
        overflow: hidden;
        border: 1px solid #DBE2DF;
        background: #FBFDFC;
        cursor: pointer;
        transition: .5s;
        .article-content {
            padding: 0 12px 12px 12px;
            gap: 15px;
        }
        .ic-flex {
            font-weight: 300;
            padding: 0 0 0 25px;
        }
        .phan-xa {
            color: var(--color);
            background: url(images/icons/ic-nao.png) no-repeat;
        }
        .muc-tieu {
            color: #A674F8;
            background: url(images/icons/ic-muctieu.png) no-repeat;
        }
        .update-new {
            color: #F9A826;
            background: url(images/icons/ic-star.png) no-repeat;
        }
        .selected {
            color: #A674F8;
            background: url(images/icons/ic-selected.png) no-repeat;
        }
        .bar {
            border-radius: 50px;
            background: #F1F8F5;
            height: 5px;
            width: 100%;
            position: relative;
            span {
                border-radius: 50px;
                background: linear-gradient(279deg, #A0D9BA 0%, #2E8B57 100%);
                position: absolute;
                height: 5px;
                left: 0;
            }
        }
        .bottom-flex {
            display: flex;
            justify-content: space-between;
            width: 100%;
            align-items: center;
            gap: 5px;
            .flex {
                display: flex;
                align-items: center;
                gap: 4px;
            }
            .btn-gradient {
                height: 28px;
                padding: 0 6px;
                span {
                    font-size: 13px;
                }
            }
            .btn-default {
                font-size: 13px;
                height: 28px;
                padding: 0 7px;
                border-radius: 2px;
                span {
                    font-size: 13px;
                }
            }
            .free {
                font-weight: 300;
                border-radius: 2px;
                color: var(--colormain);
                background: #F1F8F5;
                padding: 4px 8px;
            }
            .view {
                font-weight: 300;
                display: flex;
                align-items: center;
                gap: 4px;
                color: #656867;
            }
            &.line-top {
                border-top: 1px solid #DBE2DF;
                padding-top: 15px;
            }
        }
        &:hover {
            transform: translateY(-10px);
            box-shadow: 2px 4px 10px 0 rgba(0, 0, 0, .2);
        }
    }
    @media (max-width: 1700px) {
        &.grid {
            grid-template-columns: repeat(3, 1fr);
            row-gap: 30px;
            .article-itemt {
                &:nth-child(7),
                &:nth-child(8) {
                    display: none;
                }
            }
        }
    }
    @media (max-width: 1480px) {
        .article-itemt {
            .article-content {
                padding: 0 10px 10px 10px;
            }
            .bottom-flex {
                .flex {
                    gap: 4px;
                }
            }
        }
    }
    @media (max-width: 1240px) {
        .article-itemt {
            .bottom-flex {
                .btn-default {
                    display: none;
                }
            }
        }
    }
    @media (max-width: 1025px) {
        .article-itemt {
            .bottom-flex {
                .btn-default {
                    display: flex;
                }
            }
        }
        &.grid {
            grid-template-columns: repeat(2, 1fr);
            column-gap: 20px;
            row-gap: 20px;
            .article-itemt {
                &:nth-child(7),
                &:nth-child(8) {
                    display: flex;
                }
            }
        }
    }
    @media (max-width: 600px) {
        .article-itemt {
            gap: 8px;
            .article-content {
                gap: 8px;
                padding: 0 8px 8px 8px;
                .title_new {
                    font-size: 13px;
                }
            }
            .bar {
                height: 3px;
                span {
                    height: 3px;
                }
            }
            .ic-flex {
                font-size: 12px;
                background-size: 18px;
                padding-left: 22px;
            }
            .bottom-flex {
                flex-wrap: wrap;
                .flex {
                    width: 100%;
                }
                .btn-gradient {
                    width: calc(50% - 2px);
                    span {
                        font-size: 12px;
                        gap: 2px;
                        .ic {
                            width: 16px;
                            height: 16px;
                            background-size: contain !important;
                        }
                    }
                    &.live {
                        span {
                            font-size: 0;
                            position: relative;
                            &::before {
                                content: "Live";
                                font-size: 13px;
                            }
                        }
                    }
                    &.video {
                        .ic {
                            margin-top: 2px;
                        }
                    }
                }
                .btn-default {
                    width: 100%;
                }
            }
        }
        &.grid {
            column-gap: 15px;
            row-gap: 15px;
        }
        &.horizontal {
            grid-template-columns: repeat(1, 1fr);
            background: #fff;
            padding: 6px;
            column-gap: 12px !important;
            row-gap: 12px !important;
            .article-itemt {
                gap: 12px;
                flex-wrap: nowrap;
                border: 0;
                border-radius: 0;
                &.article-itemt+.article-itemt {
                    border-top: 1px dashed #DBE2DF;
                    padding-top: 12px;
                }
                .article-thumb {
                    width: 140px;
                    flex-shrink: 0;
                }
                .article-content {
                    padding: 0;
                    .title_new {
                        font-size: 14px;
                    }
                }
                .ic-flex {
                    display: none;
