            top: 0;
            bottom: 0;
            left: 28px;
            span {
                color: #656867;
                width: 26px;
                height: 26px;
                display: flex;
                justify-content: center;
                align-items: center;
                border: 3px solid #DBE2DF;
                border-radius: 50%;
                background: #fff;
                position: absolute;
                z-index: 1;
                top: 50%;
                left: 50%;
