                font-weight: 300;
                display: flex;
                align-items: center;
                gap: 4px;
                position: relative;
                .icon-svg {
                    width: 20px;
                    flex-shrink: 0;
                }
                &.item+.item {
                    &::before {
                        content: "";
                        background: #dbe2df;
                        width: 4px;
                        height: 4px;
                        position: absolute;
                        border-radius: 50%;
                        left: -15px;
                    }
                }
            }
        }
        &::after {
            content: "";
            background: #DBE2DF;
            position: absolute;
            height: 1px;
            right: 0;
            left: 52px;
            bottom: 0;
