                font-size: 15px;
            }
            .flex {
                font-size: 13px;
                font-weight: 300;
            }
        }
        .lesson {
            padding: 8px 8px 8px 42px;
            .title {
                font-size: 14px;
                font-weight: 500;
            }
            .lesson-step {
                left: 20px;
            }
            .info-lesson {
                column-gap: 15px;
                row-gap: 15px;
                font-size: 12px;
                .item {
                    .icon-svg {
                        width: 16px;
                    }
                    &.item+.item {
                        &::before {
                            left: -8px;
                        }
                    }
                }
            }
        }
    }
}

