    &__right {
        width: 50%;
        min-height: 100dvh;
    }
    &__left{
        background: url(images/graphics/img-login.png);
        padding: 40px;
        color: #fff;
        background-size: cover;
        background-position: bottom center;
        .logo{
            font-size: 18px;
            font-weight: bold;
